.login-page{
    background: #e6e2e1;
    height: auto;
}

.login-box {
    background: #fff;
    width: 320px;
}
.login-logo{
    max-width: 320px;
    max-height: 70px;
    margin-bottom: 0;
    padding-top: 5px;
}

.login-box-msg{
    font-weight: 600;
    padding: 0 0 20px 0;
}

.forgot-link{
    display: block;
    padding-top: 10px;
    text-align: right;
}
.login-link{
    display: block;
    padding-top: 10px;
    text-align: left;
}
.login-button{
    background: #008967;
    color:#fff;
}
.login-button:hover{
    color:#fff;
    background: #11a27e;
}

.main-header {
    .logo {
        transition: width 0.3s ease-in-out;
        display: block;
        float: left;
        height: 69px;
        font-size: none;
        line-height: 50px;
        text-align: center;
        width: 269px;
        font-family: none;
        padding: 0;
        font-weight: none;
        overflow: hidden;
        background: #fff;

        .logo-lg {

            img {
                width: 100%;
                height: 100%;
            }
        }
        .logo-mini {

            img {
                width: 50%;
                height: 50%;
            }
        }
    }
}
.skin-blue .main-sidebar{
    width: 269px;
    padding-top: 69px;
}
.skin-blue .wrapper,
.skin-blue .main-sidebar,
.skin-blue .left-side {
    background-color:#ffffff;
}
.skin-blue .sidebar-menu > li.header {
    color: #2f70af;
    background-color: #ffffff;
}
.skin-blue .sidebar .sidebar-menu .treeview-menu{
    margin: 0px;
    padding: 0px;
}
.skin-blue .sidebar .sidebar-menu .treeview-menu > li {
    background-color: #ffffff;
}
.skin-blue .sidebar .sidebar-menu .treeview-menu > li.active, .skin-blue .sidebar .sidebar-menu > li.active:not(:has(li.active)) {
    background-color: #e8f2ff;
}

.skin-blue .treeview-menu > li > a {
    color: #999999;
    margin-left: 15px;
}
.skin-blue .treeview-menu > li > .treeview-menu li a {
    margin-left: 30px;
}
.skin-blue .sidebar .sidebar-menu .treeview-menu > li > a:hover {
    color: #2f70af;
}
.skin-blue .sidebar-menu > li > a > i , .pull-right{
    color: #7e7e7e;
}
.skin-blue .sidebar-menu > li.active > a,.skin-blue .sidebar-menu > li.active > a > i {
    color: #2f70af;
    border-left-color: none;
}
.skin-blue .sidebar-menu > li > a,.skin-blue .sidebar-menu > li > a > i {
    transition: font-size 0.5s ease;
}
.skin-blue.sidebar-collapse .sidebar-menu > li > a {
    padding: 11px 12px;
}
.skin-blue.sidebar-collapse .sidebar-menu > li > a > i {
    font-size: 19px;
}
.skin-blue .sidebar .sidebar-menu .treeview-menu > li > a > i {
    color: #7e7e7e;
}
.skin-blue .sidebar-menu > li:hover > a,
.skin-blue .sidebar-menu > li:hover > a > i,
.skin-blue .sidebar-menu > li:hover > a > span > i,
.skin-blue .sidebar-menu > li.menu-open > a,
.skin-blue .sidebar .sidebar-menu .treeview-menu > li:hover > a > i
 {
    color: #2f70af;
}
.skin-blue .control-sidebar-dark, .skin-blue .control-sidebar-dark + .control-sidebar-bg {
    background: #ffffff;
}

.sidebar-menu > li > a, .sidebar-menu > li > ul.treeview-menu > li.active > a > span,
.skin-blue .sidebar .sidebar-menu .treeview-menu > li.active > a > i
{
    color: #2f70af;
}
.main-header .navbar {
    min-height: 69px;
}
.main-header .navbar .nav > li > a {
    color: #ffffff;
    font-size: 20px;
}

.notification .img-circle {
    height: 30px !important;
    width: 30px !important;
}
.language-img {
    margin: 0px;
    padding: 0px;
    height: 14px;
    width: 22px;
    padding: 0px 0px 0px 0px;
}
.navbar-nav > .lang-menu > .dropdown-menu {
    width: 130px;
}
.navbar-nav > .lang-menu > .dropdown-menu > li.header {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
    background-color: #ffffff;
    padding: 7px 10px;
    border-bottom: 1px solid #f4f4f4;
    color: #444444;
    font-size: 14px;
}
.navbar-nav > .lang-menu > .dropdown-menu > li > a {
    font-size: 12px;
    color: #444444;
    border-radius: 4px;
    line-height: 20px;
    margin: 0;
    padding: 10px 5px;
}
.navbar-nav > .lang-menu > .dropdown-menu > li > a > div > img {
    border: 1px solid #dddddd;
    height: 20px;
    margin: auto 10px auto 0px;
    width: 30px;
}
.navbar-nav > .lang-menu > .dropdown-menu > li > a > h4 {
    font-size: 12px;
    margin: 0;
    padding: 0px;
}
.navbar-nav > .user-menu > .dropdown-menu > .user-body {
    border-top: none !important;
}

.navbar-nav > .user-menu > .dropdown-menu > .user-body > .password {
    border-left: 2px solid #2196f3;
}
.navbar-nav > .user-menu > .dropdown-menu > .user-footer > .password {
    border-left: 2px solid;
}
.navbar-nav > .user-menu > .dropdown-menu > li.user-footer {
    padding: 10px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    -webkit-border-bottom-left-radius: 5px;
    -webkit-border-bottom-right-radius: 5px;
    -moz-border-bottom-left-radius: 5px;
    -moz-border-bottom-right-radius: 5px;
    background-color: #2196f3;
    color: #fff;
}
.navbar-nav > .user-menu > .dropdown-menu > li.user-footer > div.text-center > a {
    color: #fff;
}

