/*
* Dashbaord CSS
*/
.small-box {
    position: relative;
    display: block;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
    margin-bottom: 15px;
    background-color: #FFFFFF;
    border-radius: 4px;
}
.small-box > .small-box-footer {
    position: relative;
    text-align: center;
    padding: 3px 0;
    color: #fff;
    color: rgba(255, 255, 255, 0.8);
    display: block;
    z-index: 10;
    background: rgba(0, 0, 0, 0.1);
    text-decoration: none;
    background-color: #FFFFFF;
    padding: 10px;
    text-align: left;
    border-radius: 4px;

}
.bg-orange-dark {
    background-color: #FF8A65 !important;
    color: #fff !important;
}
.small-box .icon {
    position: absolute;
    top: auto;
    bottom: 0px;
    right: 0px;
    z-index: 0;
    font-size: 60px;
    padding: 9.5px 25px 8px 25px;
    color: rgba(0, 0, 0, 0.15);
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;

}
.small-box h3 {
    font-size: 38px;
    font-weight: bold;
    margin: 0 0 10px 0;
    white-space: nowrap;
    padding: 0;
    color: #c6cad6;
}
.text-white {
    color: #FFF !important;
}
.small-box p {
    font-size: 15px;
    color: #c6cad6;
}
.bg-pink-light {
    background-color: #F06292 !important;
    color: #fff !important;
}
.bg-purple-light {
    background-color: #9575CD !important;
    color: #fff !important;
}
.bg-teal-light {
    background-color: #4DB6AC !important;
    color: #fff !important;
}


