/**
 Mobile view element hide css
 */
@media screen and (max-width: 1024px) {
    .tablet-hidden {
        visibility: hidden;
        clear: both;
        display: none !important;
    }
}


#leaveStats .tile_count .tile_stats_count:first-child:before {
